.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  &--desktop {
    grid-template-columns: repeat(1, 1fr);
  }

  @include onTab {
    grid-template-columns: repeat(6, 1fr);

    .grid__item {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--md--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;

    .grid__item {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--lg--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }

  &__description {
    display: grid;
    grid-template-columns: repeat(1, 1fr);

    .grid__item {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--md--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @include onDesktop {
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;

      .grid__item {
        @for $start from 1 through 12 {
          @for $end from $start through 12 {
            &--lg--#{$start}-#{$end} {
              grid-column: #{$start} / #{$end + 1};
            }
          }
        }
      }
    }
  }
}
